.header {
  @include flex(row, center, center);
  background: #B9D4ED url("../img/clouds.png");
  width: 100%;
  height: 188px;
  .container {
    @include flex(row, space-between, flex-start);
    @media (max-width: 768px) {
      flex-wrap: wrap;
    }
    .column {
      @include flex(column, flex-start, flex-start);
      .f-wrapper,
      .header-contacts {
        @include flex(row, space-between, center);
        margin-top: 32px;
        margin-bottom: 28px;
      }
      .f-wrapper {
        .lang-tabs {
          margin-right: 16px;
          a {
            @include font-style ($font-size: 16px, $font-weight: 600, $color: #1F1C39, $line-height: 24px);
            display: inline-block;
            text-decoration: none;
            padding: 4px 12px;
            border-radius: 4px;
            &:hover {
              background: #fff;
            }
            &.current {
              background: #383085;
              color: #E4E2F2;
            }
          }
        }
        .search {
          @media (max-width: 425px) {
            display: none;
          }
          form {
            position: relative;
            input {
              @include font-style ($font-size: 14px, $color: #1F1C39, $line-height: 16px);
              width: 228px;
              height: 32px;
              background: #fff;
              border-radius: 4px;
              border: none;
              padding: 8px 32px 8px 8px;
              outline: none;
              box-shadow: 0px 4px 12px rgba(56, 48, 133, 0.1);
              &:placeholder-shown {
                box-shadow: none;
              }
              &:hover {
                box-shadow: 0px 4px 12px rgba(56, 48, 133, 0.1);
              }
              &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
                color: #7D7B91;
              }
              &::-moz-placeholder { /* Firefox 19+ */
                color: #7D7B91;
              }
              &:-ms-input-placeholder { /* IE 10+ */
                color: #7D7B91;
              }
              &:-moz-placeholder { /* Firefox 18- */
                color: #7D7B91;
              }
            }
            .search-btn {
              @include position(absolute, 4px, 8px, auto, auto, 24px, 24px);
              outline: none;
              border: none;
              cursor: pointer;
              background: transparent url("../img/svg/search-icn.svg");
            }
          }
        }
      }
      .logo {
        max-width: 311px;
        margin-top: 32px;
        img {
          max-width: 100%;
        }
        @media (max-width: 1025px) {
          max-width: 210px;
          margin-top: 80px;
        }
      }
      .header-contacts {
        a {
          @include font-style ($font-size: 24px, $font-weight: 600, $color: #1F1C39, $line-height: 32px);
          display: inline-block;
          text-decoration: none;
          padding: 4px 12px;
          &.fb,
          &.vk {
            width: 24px;
            height: 24px;
            border-radius: 5px;
            margin-left: 8px;
            @media (max-width: 425px) {
              display: none;
            }
          }
          &.fb {
            background: url("../img/svg/fb-icn.svg");
          }
          &.vk {
            background: url("../img/svg/vk-icn.svg");
          }
        }
        .mail {
          @include font-style ($font-size: 24px, $font-weight: 600, $color: #1F1C39, $line-height: 32px);
          display: inline-block;
          margin-left: 24px;
          border-bottom: 1px dashed #d2d2d7;
          cursor: pointer;
          &:hover {
            color: #F24D4D;
          }
          @media (max-width: 425px) {
            font-size: 18px;
            line-height: 24px
          }
        }
        .burger {
          display: none;
          width: 32px;
          height: 20px;
          position: relative;
          margin-left: 26px;
          cursor: pointer;
          z-index: 12;
          span {
            width: 32px;
            height: 2px;
            background: #383085;
            transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                        background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                        opacity 0.55s ease;
          }
          &:before,
          &:after {
            width: 32px;
            height: 2px;
            background: #383085;
            position: absolute;
            content: '';
            transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                        background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                        opacity 0.55s ease;
          }
          &:before {
            top: 0;
            transform-origin: 0% 0%;
          }
          &:after {
            bottom: 0;
            transform-origin: 0% 100%;
          }
          &.expanded {
            &:before,
            &:after {
              opacity: 1;
              transform: rotate(45deg) translate(-4px, -1px);
              background: #E4E2F2;
            }
            span {
              opacity: 0;
              transform: rotate(0deg) scale(0.2, 0.2);
            }
            &:after{
              transform: rotate(-45deg) translate(-2px, -1px);
            }
          }
          @media (max-width: 768px) {
            @include flex(column, center, center);
          }
        }
      }
      &.last {
        align-items: flex-end;
      }
      @media (max-width: 768px) {
        &.first {
          order: 1
        }
        &.second {
          order: 3;
          width: 100%;
          align-items: center;
          .logo {
            margin-top: -20px;
          }
        }
        &.last {
          order: 2
        }
      }
    }
    .rosel {
      width: 179px;
      @media (max-width: 768px) {
        display: none;
      }
    }
    .vega {
      width: 182px;
      @media (max-width: 768px) {
        display: none;
      }
    }
  }
}
