h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

@font-face {
    font-family: 'Golos';
    src: local('Golos Text DemiBold'), local('GolosText-DemiBold'),
    url('../fonts/GolosText-DemiBold.woff2') format('woff2');
    font-weight: 600;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Golos';
    src: local('Golos Text Regular'), local('GolosText-Regular'),
    url('../fonts/GolosText-Regular.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Golos';
    src: local('Golos Text Medium'), local('GolosText-Medium'),
    url('../fonts/GolosText-Medium.woff2') format('woff2');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Golos';
    src: local('Golos Text Bold'), local('GolosText-Bold'),
    url('../fonts/GolosText-Bold.woff2') format('woff2');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

* {
    -moz-box-sizing: border-box; /* Для Firefox */
    box-sizing: border-box; /* Ширина блока с полями */
}
html {}
body {
    @include font-style ($font-size: 16px, $color: #1F1C39, $line-height: 24px);
    background: #fff url("../img/circle-bg.png") no-repeat 50% 350px;
    &.inside {
        background-position: 50% 0;
    }
}

.container {
    width: 100%;
    height: 100%;
    max-width: 1280px;
    padding: 0 20px;
}

.button {
    @include font-style ($font-size: 16px, $font-weight: 600, $color: #E4E2F2, $line-height: 24px);
    margin-top: 24px;
    text-decoration: none;
    display: inline-block;
    background: #383085;
    border-radius: 4px;
    padding: 14px 32px;
    border: 0;
    cursor: pointer;
    &:hover {
        background: #F24D4D;
    }
}

.clip {
    overflow: hidden !important;
}