.main-content {
  @include flex(row, center, center);
  .container {
    @include flex(column, center, flex-start);
    padding: 0;
    max-width: 1264px;
    @media (max-width: 1281px) {
      padding: 0 30px;
      max-width: 100%;
      align-items: center;
    }
    h1 {
      @include font-style ($font-family: 'Montserrat Alternates', $font-size: 36px, $font-weight: bold, $color: #383085, $line-height: 48px);
      width: 100%;
      text-align: center;
      margin-bottom: 53px;
    }
    .content-wrapper {
      @include flex(row, center, flex-start);
      width: 100%;
      margin-bottom: 84px;
      .left {
        min-width: 400px;
        max-width: 400px;
        margin-right: 30px;
        @media (max-width: 1281px) {
          min-width: 350px;
          max-width: 350px;
        }
        @media (max-width: 1025px) {
          min-width: 300px;
          max-width: 300px;
        }
        @media (max-width: 768px) {
          display: none;
        }
        .director {
          margin-bottom: 34px;
          img {
            border-radius: 50%;
          }
          .name {
            @include font-style ($font-size: 16px, $font-weight: 600, $color: #1F1C39, $line-height: 24px);
            margin-bottom: 4px;
          }
          .position {
            @include font-style ($font-size: 16px, $font-weight: normal, $color:#7D7B91, $line-height: 24px);
            margin-bottom: 18px;
          }
          .message {
            @include font-style ($font-size: 16px, $font-weight: normal, $color: #1F1C39, $line-height: 24px);
          }
        }
        .banner {
          margin-bottom: 34px;
        }
      }
      .right {
        width: 100%;
        .rich-links {
          @include flex(row, center, flex-start);
          margin-bottom: 56px;
          @media (max-width: 425px) {
            justify-content: space-between;
          }
          a {
            @include flex(column, center, center);
            margin: 0 70px;
            text-decoration: underline;
            text-decoration-color: #d2d2d7;
            &:hover {
              text-decoration: none;
            }
            @media (max-width: 1281px) {
              margin: 0 40px;
            }
            @media (max-width: 425px) {
              max-width: 78px;
              margin: 0;
            }
            img {
              margin-bottom: 8px;
              @media (max-width: 425px) {
                width: 64px;
                height: 64px;
              }
            }
            span {
              @include font-style ($font-size: 18px, $font-weight: 600, $color: #1F1C39, $line-height: 24px);
              text-align: center;
              @media (max-width: 425px) {
                font-size: 12px;
                line-height: 16px;
              }
            }
          }
        }
        .news {
          @include flex(row, space-between, flex-start);
          flex-wrap: wrap;
          .news-item {
            @include flex(column, flex-start, flex-start);
            width: 50%;
            max-width: 400px;
            min-height: 400px;
            border-radius: 4px;
            overflow: hidden;
            margin-bottom: 32px;
            align-content: flex-start;
            &:first-child {
              position: relative;
              padding: 24px;
              justify-content: flex-end;
              .news-img {
                @include position(absolute, 0, 0, 0, 0, 100%, 100%);
                background-size: cover;
                background-position: center;
                z-index: 0;
                margin-bottom: 16px;
              }
              &:before {
                @include position(absolute, 0, 0, 0, 0, 100%, 100%);
                z-index: 1;
                background: linear-gradient(180deg, rgba(56, 48, 133, 0.4) 0%, rgba(56, 48, 133, 0.95) 100%);
                content: '';
              }
              .title {
                a {
                  @include font-style ($font-size: 18px, $font-weight: 600, $color: #fff, $line-height: 24px);
                  position: relative;
                  z-index: 2;
                  text-decoration: none;
                  &:hover {
                    color: #F24D4D;
                  }
                }
              }
              .description {
                @include font-style ($font-size: 16px, $font-weight: normal, $color: #fff, $line-height: 24px);
                position: relative;
                z-index: 2;
              }
              .date {
                @include font-style ($font-size: 14px, $font-weight: normal, $color: #E4E2F2, $line-height: 16px);
                position: relative;
                z-index: 2;
                opacity: .6;
              }
            }
            .news-img {
              @include position(relative, 0, 0, 0, 0, 100%, 225px);
              background-size: cover;
              background-position: center;
              margin-bottom: 16px;
              min-height: 225px;
            }
            .title {
              a {
                @include font-style ($font-size: 18px, $font-weight: 600, $color: #1F1C39, $line-height: 24px);
                position: relative;
                z-index: 2;
                text-decoration: none;
                &:hover {
                  color: #F24D4D;
                }
              }
            }
            .description {
              @include font-style ($font-size: 16px, $font-weight: normal, $color: #4B4960, $line-height: 24px);
              position: relative;
              z-index: 2;
            }
            .date {
              @include font-style ($font-size: 14px, $font-weight: normal, $color: #7D7B91, $line-height: 16px);
              position: relative;
              z-index: 2;
            }
            @media (max-width: 1281px) {
              min-width: 370px;
              max-width: 370px;
            }
            @media (max-width: 1025px) {
              min-width: 300px;
              max-width: 300px;
            }
            @media (max-width: 425px) {
              min-width: 100%;
              max-width: 100%;
            }
          }
        }
      }
    }
  }
}
