.modal-wrapper {
  @include position(fixed, 0, 0, 0, 0, 100%, 100%);
  z-index: 12;
  display: none;
  background: rgba(22%, 19%, 52%, 0.9);
  form {
    @include flex(column, flex-start, flex-start);
    max-width: 484px;
    width: 100%;
    height: 428px;
    background: #E7EBF7;
    border-radius: 4px;
    padding: 56px;
    position: relative;
    .close-modal {
      @include position(absolute, 0, 0, auto, auto, 52px, 52px);
      padding: 20px;
      background: #FFFFFF;
      border-radius: 4px;
      cursor: pointer;
      &:before,
      &:after {
        display: block;
        width: 15px;
        height: 2px;
        background: #7D7B91;
        content: '';
      }

      &:before {
        transform: rotate(45deg) translate(2px, 5px);
      }
      &:after {
        transform: rotate(-45deg) translate(-3px, 1px);
      }
    }
    h3 {
      @include font-style ($font-size: 24px, $font-weight: 600, $color: #383085, $line-height: 32px);
      width: 100%;
      text-align: center;
      margin-bottom: 32px;
    }
    .text-field {
      margin-bottom: 16px;
      input {
        @include font-style ($font-size: 14px, $color: #1F1C39, $line-height: 16px);
        width: 300px;
        height: 52px;
        background: #fff;
        border-radius: 4px;
        border: none;
        padding: 8px 32px 8px 8px;
        outline: none;
        box-shadow: 0px 4px 12px rgba(56, 48, 133, 0.1);
        &:placeholder-shown {
          box-shadow: none;
        }
        &:hover {
          box-shadow: 0px 4px 12px rgba(56, 48, 133, 0.1);
        }
        &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
          color: #7D7B91;
        }
        &::-moz-placeholder { /* Firefox 19+ */
          color: #7D7B91;
        }
        &:-ms-input-placeholder { /* IE 10+ */
          color: #7D7B91;
        }
        &:-moz-placeholder { /* Firefox 18- */
          color: #7D7B91;
        }
      }
    }
    .button {
      margin-top: 8px;
    }
    .privacy {
      @include font-style ($font-size: 14px, $font-weight: normal, $color: #7D7B91, $line-height: 16px);
      margin-top: 24px;
      a {
        text-decoration: none;
      }
    }
    @media (max-width: 425px) {
      padding: 56px 22px;
      margin: auto 20px;
    }
    @media (max-width: 377px) {
      margin: auto 7px;
    }
  }
  &.open {
    @include flex(row, center, center);
  }
}
