.product-gallery {
  @include flex(row, center, center);
  padding: 56px 0;
  margin-bottom: 28px;
  .container {
    @include flex(column, center, flex-start);
    padding: 0;
    max-width: 1264px;
    @media (max-width: 1281px) {
      max-width: 100%;
      align-items: center;
      padding: 0 20px;
    }
    @media (max-width: 1025px) {
      padding: 0 30px;
    }
    h1 {
      width: 100%;
      text-align: center;
      margin-bottom: 40px;
      a {
        @include font-style ($font-family: 'Montserrat Alternates', $font-size: 36px, $font-weight: bold, $color: #383085, $line-height: 48px);
        text-decoration: none;
        border-bottom: 1px solid #d7d6e7;
        &:hover {
          border-bottom-color: transparent;
        }
      }
    }
    .gallery-wrapper {
      position: relative;
      width: 1252px;
      @media (max-width: 1281px) {
        width: calc(100% - 20px);
      }
      .siema {
        width: 1280px;
        @media (max-width: 1281px) {
          width: 100%;
          overflow: hidden;
        }
        .gallery-item {
          width: 292px;
          margin-right: 32px;
          @media (max-width: 1281px) {
            width: 270px;
            height: 270px;
            margin-right: 20px;
          }
          @media (max-width: 1025px) {
            width: 216px;
            height: 216px;
            overflow: hidden;
          }
          @media (max-width: 425px) {
            width: 100%;
            height: auto;
            overflow: hidden;
          }
        }
        img {
          width: 100%;
          height: 292px;
          margin-bottom: 16px;
        }
        a {
          @include font-style ($font-size: 18px, $color: #1F1C39, $line-height: 24px);
          display: inline-block;
          text-decoration: none;
        }
      }

      .prev,
      .next {
        border: none;
        outline: none;
        padding: 0;
        margin: 0;
        cursor: pointer;
        @media (max-width: 1025px) {
          top: 83px;
        }
      }
      .prev {
        @include position(absolute, 123px, auto, auto, -88px, 56px, 56px);
        background: transparent url("../img/svg/arr-left.svg");
        @media (max-width: 1281px) {
          left: -30px;
        }
        @media (max-width: 1025px) {
          left: -20px;
        }
        &:hover {
          background: transparent url("../img/svg/arr-left-red.svg");
        }
      }
      .next {
        @include position(absolute, 123px, -88px, auto, auto, 56px, 56px);
        background: transparent url("../img/svg/arr-right.svg");
        @media (max-width: 1281px) {
          right: -30px
        }
        @media (max-width: 1025px) {
          right: -20px
        }
        &:hover {
          background: transparent url("../img/svg/arr-right-red.svg");
        }
      }
    }
  }
}
