.footer {
  @include flex(row, center, center);
  background-color: #383085;
  background-image: url("../img/circle-footer.png"), url("../img/clouds-footer.png");
  background-repeat: no-repeat;
  background-position: center bottom;
  min-height: 300px;
  .container {
    @include flex(row, space-between, flex-start);
    padding: 26px 0;
    @media (max-width: 1281px) {
      max-width: 100%;
      padding: 0 30px;
    }
    .column {
      @include flex(column, flex-start, flex-start);
      max-width: 500px;
      height: 100%;
      nav {
        @include flex(row, flex-start, flex-start);
        ul {
          list-style: none;
          padding: 0;
          li {
            a {
              display: inline-block;
              margin-bottom: 10px;
              text-decoration: none;
              &:hover {
                color: #F24D4D;
                opacity: 1;
              }
            }
          }
        }
        >ul {
          margin-left: 56px;
          &:first-child {
            margin-left: 0;
          }
          >li {
            >a {
              @include font-style ($font-size: 14px, $font-weight: 600, $color: #E4E2F2, $line-height: 16px);
            }
            ul {
              li {
                a {
                  @include font-style ($font-size: 14px, $font-weight: normal, $color: #E4E2F2, $line-height: 16px);
                  opacity: .6;
                  margin-bottom: 4px;
                }
              }
            }
          }
        }
      }
      .hotline {
        margin-top: 24px;
        width: 302px;
        img {
          margin-right: 12px;
        }
        a {
          @include flex(row, flex-start, flex-start);
          .text {
            @include font-style ($font-size: 12px, $font-weight: normal, $color: #E4E2F2, $line-height: 16px);
            flex-wrap: wrap;
            opacity: .6;
            text-decoration: none;
            b {
              @include font-style ($font-size: 18px, $font-weight: 600, $color: #E4E2F2, $line-height: 24px);
              display: block;
              text-transform: uppercase;
            }
          }
          &:hover {
            .text {
              opacity: 1;
            }
          }
        }
      }
      .mail {
        @include font-style ($font-size: 24px, $font-weight: 600, $color: #E4E2F2, $line-height: 32px);
        display: inline-block;
        text-decoration: none;
        margin-bottom: 16px;
        border-bottom: 1px dashed #d2d2d7;
        cursor: pointer;
        &:hover {
          color: #F24D4D;
        }
        @media (max-width: 425px) {
          font-size: 18px;
          line-height: 24px
        }
      }
      .logo {
        max-width: 311px;
        margin: 32px 0 12px;
        img {
          max-width: 100%;
        }
      }
      &:last-child {
        @include font-style ($font-size: 24px, $font-weight: 600, $color: #E4E2F2, $line-height: 32px);
        align-items: flex-end;
        justify-content: space-between;
        .map,
        .copy {
          @include font-style ($font-size: 14px, $font-weight: normal, $color: #E4E2F2, $line-height: 16px);
          opacity: 0.6;
        }
        .map {
          a {
            position: relative;
            color: #E4E2F2;
            text-decoration: none;
            margin-right: 20px;
            display: inline-block;
            border-bottom: 1px solid rgba(228, 226, 242, 0.2);
            &:hover {
              color: #fff;
              opacity: 1;
            }
            &:after {
              position: absolute;
              right: -20px;
              top: 0;
              display: block;
              width: 16px;
              height: 16px;
              content: '';
              background: url("../img/svg/map-pin.svg");
            }
          }
        }
      }
      @media (max-width: 768px) {
        &:nth-child(2) {
          display: none;
        }
      }
    }
    @media (max-width: 425px) {
      flex-direction: column;
      nav {
        flex-wrap: wrap;
        ul {
          width: 50%;
          margin-left: 0 !important;
          margin-bottom: 10px;
          &:last-child {
            width: 100%;
          }
        }
      }
      .column {
        &:last-child {
          margin-top: 20px;
          align-items: center;
          padding-bottom: 30px;
        }
      }
    }
  }
}
