.product-list {
  @include flex(row, center, center);
  .container {
    @include flex(column, flex-start, flex-start);
    padding: 56px 0 24px;
    max-width: 1060px;
    h1 {
      @include font-style ($font-family: 'Montserrat Alternates', $font-size: 36px, $font-weight: bold, $color: #383085, $line-height: 48px);
      width: 100%;
      text-align: center;
      margin-bottom: 53px;
    }
    .products {
      .product-item {
        @include flex(row, flex-start, flex-start);
        margin-bottom: 32px;
        img {
          width: 292px;
          height: 292px;
          margin-right: 32px;
          border-radius: 4px;
        }
        .text {
          @include font-style ($font-size: 16px, $font-weight: normal, $color: #1F1C39, $line-height: 24px);
          flex-wrap: wrap;
          text-decoration: none;
          h2 {
            @include font-style ($font-size: 24px, $font-weight: 600, $color: #1F1C39, $line-height: 32px);
            margin-bottom: 16px;
          }
          ul {
            list-style: none;
            li {
              padding-left: 20px;
              position: relative;
              &:before {
                position: absolute;
                content: '\2014';
                margin-left: -20px;
              }
            }
          }
        }
      }
    }
  }
}
