.geography {
  @include flex(row, center, center);
  .container {
    @include flex(column, center, center);
    padding: 0;
    max-width: 1264px;
    @media (max-width: 1281px) {
      max-width: 100%;
      padding: 0 30px;
    }
    h1 {
      @include font-style ($font-family: 'Montserrat Alternates', $font-size: 36px, $font-weight: bold, $color: #383085, $line-height: 48px);
      width: 100%;
      text-align: center;
    }
    p {
      @include font-style ($font-size: 18px, $font-weight: normal, $color: #7D7B91, $line-height: 28px);
      text-align: center;
      margin-top: 4px;
    }
    .geography {
      margin-top: 40px;
      margin-bottom: 56px;
      max-width: 100%;
    }
  }
}
