.promo {
  @include flex(row, center, center);
  background: #383085 url("../img/promo.png");
  background-position: center;
  background-size: cover;
  height: 460px;
  @media (max-width: 1025px) {
    height: 245px;
  }
  @media (max-width: 768px) {
    background: #383085 url("../img/promo-sm.png");
    background-position: center;
    background-size: cover;
  }
  .container {
    @include flex(row, center, center);
    h1 {
      @include font-style ($font-family: 'Montserrat Alternates', $font-size: 52px, $font-weight: bold, $color: #fff, $line-height: 56px);
      max-width: 1264px;
      text-align: center;
      @media (max-width: 1025px) {
        font-size: 36px;
        line-height: 48px;
      }
      @media (max-width: 768px) {
        font-size: 28px;
        line-height: 32px;
      }
    }
  }
}
