.navigation-bar {
  @include flex(row, center, center);
  background: #383085;
  height: 56px;
  .container {
    @include flex(row, center, center);
    a {
      @include font-style ($font-size: 16px, $font-weight: 600, $color: #E4E2F2, $line-height: 24px);
      text-transform: uppercase;
      text-decoration: none;
      margin: 0 12px;
      &:hover {
        color: #F24D4D;
      }
    }
  }
  @media (max-width: 768px) {
    display: none;
    &.expanded {
      @include flex(row, center, center);
      @include position(fixed, 0, 0, 0, 0, 100%, 100%);
      z-index: 11;
      transition: width 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                  height 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
      .container {
        @include flex(column, center, center);
        a {
          margin: 16px 0;
        }
      }
    }
  }
}
